.championCard {
    text-align: center;
    background-color: rgb(50, 55, 70);
    padding:1rem;
    border-radius: 1rem;
    h2 {
        font-size: 1.2rem;
        font-weight: bold;
    }
    h3 {
        font-size: 1rem;
        font-weight: normal;
    }
    .applyIcon {
        color:rgb(142, 255, 142);
        &:hover{
            color:green;
        }
    }
    &.vanish {
        display:none;
    }
    .vanish {
        display:none;
    }
}
.click {
    cursor: pointer;
}